import React from "react";
import {  PageContainer ,Container, Image, Quote, Text, Title, BackgroundVideo, CardContainer } from "./AboutMeStyle";
import Navbar from "./Navbar";
import MyPicture from "./ich2.jpg";
import { Fade} from 'react-awesome-reveal';
import { useTranslation } from "react-i18next";
import video from "./video.mp4";
import Skills from "./Skills";


function AboutMe() {

    const { t } = useTranslation();

    return (
        <PageContainer>
            <BackgroundVideo autoPlay loop muted playsInline src={video} type="video/mp4" />
                <Navbar />
                <CardContainer>
                <Fade delay={100}>
                <Container>
                    <Image src={MyPicture} alt="Me" />
                    <Title>{t('ber mich')}</Title>
                    <Text>{t('Mein Name ist Anna-Lena Mlln, ich komme aus der wunderschnen Stadt Hamburg und bin Fachinformatikerin fr Anwendungsentwicklung. Webseiten zu programmieren ist meine groe Leidenschaft, weil dort keine Grenzen gesetzt sind und ich meiner Kreativitt freien Lauf lassen kann. Es bereitet mir groe Freude, die Ergebnisse meiner Arbeit zu sehen und stndig Neues zu lernen. Die endlosen Mglichkeiten, die das Programmieren bietet, haben mich schon frh fasziniert und ich habe gerne Zeit am PC verbracht.')}</Text>
                    <Text>{t('Neben meiner Arbeit, gehe ich gerne zum Sport. Dies ist ein groes Hobby von mir und bietet einen wunderbaren Ausgleich zum Programmieren.')}</Text>
                    <Quote>{t("Die einzige Mglichkeit, eine neue Programmiersprache zu lernen, besteht darin, Programme in ihr zu schreiben.")}  Dennis Ritchie </Quote>
                </Container>
                </Fade>
                <Fade delay={100}>
                    <Skills/>
                </Fade>
                </CardContainer>
        </PageContainer>    
    );
}

export default AboutMe;